import React from 'react';
import './AROverlay.css';

const AROverlay = ({product}) => {
    const handleClickReset = async () => {
        /*      await scene.remove(scene.getObjectByName('model'));
              await loadModel();*/
    }

    const handleClickSize = () => {
        /* history.push(`/size/${category}`);*/
    }

    const handleClickBuy = () => {
        console.log('buy')
    }

    return (
        <>
            <div className='ar_support_container'>
                <div className='ar_info_close_container'>
                    <span className='ar_info_close'/>
                </div>
                <div className='ar_info_reset_container'>
                    <img className='ar_info_reset' src='/assets/images/other/reset.png' alt='reset'/>
                </div>
            </div>

            <div className='ar_info_container'>
                <div className='ar_info'>
                    <div className='ar_info_main'>
                        <div className='ar_info_main_title'>
                            {product.title}
                        </div>
                        <div className='ar_info_main_price'>
                            {product.price} BYN
                        </div>
                    </div>
                    <div className='ar_info_additional'>
                        <div>
                            <span>Цвет: </span>{product.color}
                        </div>
                        <div>
                            <span>Размеры, см: </span>{product.size}
                        </div>
                    </div>

                </div>
                <div className='ar_buttons'>
                    <button className='ar_button_size' onClick={handleClickSize}>Подобрать по размерам</button>
                    <button className='ar_button_buy' onClick={handleClickBuy}>Посмотреть карточку товара</button>
                </div>
            </div>
        </>
    );
}

export default AROverlay;