import React from 'react';
import './SizeInput.css';

const SizeInput = ({side}) => {
    return (
        <div className='size_input_item'>
            <label htmlFor={side[0]}>{side[1]}: </label>
            <input className='size_input' id={side[0]} type='number' placeholder='0'/>
            <span>см</span>
        </div>
    );
}

export default SizeInput;