import React from 'react';
import './Catalog.css';
import {Link} from "react-router-dom";
import {GAevent} from "../GoogleAnalytics/Events";

const Catalog = ({data}) => {
    const handleGAEventClickSize = (title) => {
        GAevent('SIZE', 'click', title);
    }

    const handleGAEventClickCategory = (title) => {
        GAevent('CATEGORY', `${title} | category click`);
    }

    const getCatalogList = () => {
        const keys = Object.keys(data);

        return keys.map(key => {
            return (
                <li key={data[key].id} className='catalog_item'>
                    <Link
                        className='item_link_category'
                        to={`/catalog/${data[key].slug}`}
                        onClick={() => handleGAEventClickCategory(data[key].title)}
                    >
                        <img className='item_link_category_image' src={data[key].image} alt={data[key].title}/>
                        <span className='item_link_category_title'>{data[key].title}</span>
                    </Link>
                    <Link
                        className='item_link_category_size'
                        to={`/size/${data[key].slug}`}
                        onClick={() => handleGAEventClickSize(data[key].title)}
                    >
                        <img
                            className='item_link_category_size_image'
                            src='./assets/images/other/ruler.png'
                            alt={data[key].title + ' size'}
                        />
                    </Link>
                </li>
            );
        });
    }

    return (
        <div className='catalog_container'>
            <div className='catalog_header'>Каталог</div>
            <ul className='catalog_list'>
                {getCatalogList()}
            </ul>
        </div>
    );
}

export default Catalog;