import React, {useRef, useState} from 'react';
import {extend, useLoader, useThree} from "@react-three/fiber";
import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader";
import {DragControls} from "three/examples/jsm/controls/DragControls";
import {useHitTest} from "@react-three/xr";

extend({DragControls});

const ARModel = ({product}) => {
    const ref = useRef();
    const {
        camera,
        gl: {domElement},
    } = useThree();

    const [select, setSelect] = useState(false);

    useHitTest((hit) => {
        hit.decompose(ref.current.position, ref.current.rotation, ref.current.scale);
    });

    const gltf = useLoader(GLTFLoader, product.arGLTF);
    const handleSelect = () => {
        gltf.scene.scale.set(1, 1, 1);
        setSelect(true);
    }

    return (
        <>
            <primitive onPointerDown={handleSelect} object={gltf.scene} scale={[0.1, 0.1, 0.1]} ref={ref}/>
            {select && <dragControls transformGroup={true} args={[[gltf.scene], camera, domElement]}/>}
        </>
    );
}

export default ARModel;