import React, {useEffect} from 'react';
import './App.css';
import data from './components/categories.json';

import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';

import Catalog from "./components/Catalog/Catalog";
import Category from "./components/Catalog/Category/Category";
import WebXR from "./components/WebXR/WebXR";
import Size from "./components/Catalog/Size/Size";
import {initGA} from "./index";


function App() {
    useEffect(() => {
        initGA();
    }, []);

    return (

        <Router>
            <Switch>
                <Route exact path='/'>
                    <Catalog data={data}/>
                </Route>
                <Route exact path='/catalog/:category'>
                    <Category data={data}/>
                </Route>
                <Route exact path='/ar/:category/:productId'>
                    <WebXR data={data}/>
                </Route>
                <Route exact path='/size/:category'>
                    <Size data={data}/>
                </Route>
            </Switch>
        </Router>
    );
}

export default App;
