import React, {useEffect, useState} from 'react';
import './Category.css';
import {useHistory, useParams} from 'react-router-dom';
import {GAevent} from "../../GoogleAnalytics/Events";

const Category = ({data}) => {
    const {category} = useParams();
    const history = useHistory();

    const handleGAEventProduct = (title) => {
        GAevent('PRODUCT', `${title} | product click`)
    }

    const [os, setOS] = useState('');

    useEffect(() => {
        const userAgent = window.navigator.userAgent;

        if (/iPhone|iPad|iPod/i.test(userAgent)) {
            setOS('apple')
        } else {
            setOS('android');
        }
    }, []);

    const handleClickLink = (productId, title) => {
        if (os === 'android') {
            history.push(`/ar/${category}/${productId}`);
        }

        handleGAEventProduct(title);
    }

    const getProduct = (product) => {
        if (os === 'apple') {
            return (
                <li key={product.id} className='category_item'>
                    <a className='category_item_apple_link' id="ar-link" href={product.usdz} rel='ar'>
                        <div className='category_item_image_container'>
                            <img className='category_item_image' src={product.image} alt={product.title}/>
                            <img className='category_item_ar_image' src='/assets/images/other/ar-link.svg' alt='ar'/>
                        </div>
                        <div className='category_item_title'>{product.title}</div>
                        <div className='category_additional_info_container'>
                            <div className='category_item_color'><span>Цвет: </span>{product.color}</div>
                            <div className='category_item_size'><span>Размеры, см: </span>{product.size}</div>
                        </div>
                        <div className='category_item_price'>{product.price} BYN</div>
                    </a>
                </li>
            )
        } else {
            return (
                <li key={product.id} className='category_item' onClick={() => {
                    handleClickLink(product.id, product.title, product.usdz);
                }}>
                    <div className='category_item_image_container'>
                        <img className='category_item_image' src={product.image} alt={product.title}/>
                        <img className='category_item_ar_image' src='/assets/images/other/ar-link.svg' alt='ar'/>
                    </div>
                    <div className='category_item_title'>{product.title}</div>
                    <div className='category_additional_info_container'>
                        <div className='category_item_color'><span>Цвет: </span>{product.color}</div>
                        <div className='category_item_size'><span>Размеры, см: </span>{product.size}</div>
                    </div>
                    <div className='category_item_price'>{product.price} BYN</div>
                </li>
            );
        }
    }

    const getCategoryProducts = () => {
        return data[category].products.map(product => {
           return  getProduct(product);
        });
    }

    return (
        <div className='category_container'>
            <div className='category_header'>{data[category].title}</div>
            <ul className='category_list'>
                {getCategoryProducts()}
            </ul>
        </div>
    );
}

export default Category;