import React from 'react';
import './Size.css';
import {useParams} from "react-router";
import {Link} from "react-router-dom";
import SizeInput from "./SizeInput";

const Size = ({data}) => {
    const {category} = useParams();

    const getInputs = () => {
        return data[category].sides.map((side, index) => {
            return <SizeInput key={index} side={side}/>
        });
    }

    return (
        <div className='size_container'>
            <div className='size_header'>Введите размеры объекта</div>

            <div className='size_image_container'>
                <img className='size_image' src={data[category].figure} alt='size'/>
            </div>

            <div className='size_input_list'>
                {getInputs()}
            </div>

            <div className='size_accept_button_container'>
                <Link to={'/'}>
                    <button className='size_accept_button' type='button'>Применить</button>
                </Link>
            </div>
        </div>
    );
}

export default Size;