import React, {useEffect, useState, Suspense} from 'react';
import './WebXR.css';
import {useParams} from "react-router";
import {ARCanvas, DefaultXRControllers, Interactive} from "@react-three/xr";
import ARModel from "./ARModel/ARModel";
import ARHitTest from "./ARHitTest/ARHitTest";
import AROverlay from "./AROverlay/AROverlay";

const WebXR = ({data}) => {

    const slug = useParams();
    const [product, setProduct] = useState([]);
    const [isHit, setIsHit] = useState(false);
    const [sessionStart, setSessionStart] = useState(false);

    useEffect(() => {
        const category = data[slug.category];
        const product = category.products.find(product => product.id === Number(slug.productId));
        setProduct(product);
        return () => {
            document.body.removeChild(document.getElementById('ARButton'));
        }
    }, []);


    const handleHit = (status) => {
        setIsHit(status);
    }

    return (
        <div className='canvas_container'>
            <ARCanvas dpr mode="concurrent" sessionInit={
                {
                    requiredFeatures: ['hit-test'],
                    optionalFeatures: ['dom-overlay'],
                    domOverlay: {root: document.querySelector('.canvas_container')}
                }
            }>
                <directionalLight position={[0, 4, 0]} intensity={1}/>
                <Interactive>
                    {!isHit && <ARHitTest onHit={handleHit}/>}
                    <Suspense fallback={null}>
                        {isHit && <ARModel product={product}/>}
                    </Suspense>
                </Interactive>
                <DefaultXRControllers/>
            </ARCanvas>
            <AROverlay product={product}/>
        </div>
    )
}

export default WebXR;